<template lang='pug'>
    .reg.clearfix
        .reg-left
            .logo
        .reg-right
            .logo
            .form
                .form-tit
                    h4 创建您的EC账户
                .form-tip
                    p
                        | 请写下来或记住这12个单词（它们的顺序和大小写-通常是小写）。该密码用于访问您的 EC 帐户。
                .form-code
                    label 创建密码：
                    input(type='password' placeholder='密码长度必须为35个字符')
                .form-code
                    label 重复密码：
                    input(type='password' placeholder='请再次输入密码')
                //- .form-tip.danger
                //-     p 
                //-         i.iconfont.icon-xinxi
                //-         | 不要透露您的密码，如果失去它您将无法访问您的帐户！
            .reg-right-btn.clearfix
                button.next 注册
                button.cancel 取消

            .reg-right-goreg
                router-link(to='/autoreg' tag='a').go-reg 想选择您自己的密码？请单击此处
        
</template>

<script>
import '$assets/scss/user/reg.scss'
export default {
    data() {
        return {
            remenber:false,
            loginType:0,
            account:'123'
        }
    },
    methods: {
        remen() {
            this.remenber=!this.remenber;
        },
        changeType(type) {
            this.loginType = type;
        },
        aaa() {
            console.log(this.account)
        }
    }
}
</script>



