<template lang='pug'>
    .chat-item.clearfix
        p.left(v-if='data.type==0') {{chat.msg}}
        p.right(v-if='data.type==1') {{chat.msg}}
        

  
</template>

<script>
import '$assets/scss/public/talkbox.scss'
// import item from './talkLeftItem'

export default {
    props:['data'],
    created() {
        // console.log(this.data);
        this.chat=this.data
    },
    updated() {
        this.chat=this.data
    },
    data() {
        return {
            tags:null,
            chat:[]
        }
    },
    methods: {
    }

}
</script>



