<template lang='pug'>
    .nav.clearfix
        .container
            .nav-logo
                router-link#logo(tag='a' to='/')
                //- a#logo(href='javascript:;')
            ul.nav-link
                li
                    router-link(to='/home' active-class='active' tag='a') {{ $t("nav.blocks") }}
                li
                    router-link(to='/peers' active-class='active' tag='a') {{ $t('nav.peers') }}
                li 
                    router-link(to='/account' active-class='active' tag='a') {{ $t('nav.account') }}
                li 
                    router-link(to='/gen' active-class='active' tag='a') Generators
                li
                    a(href='javascript:;' @click='goApi') API Console
                li 
                    a(href='javascript:;' @click='goShell') Database Shell
            ul.nav-user
                li
                    a(@click='showBox')   
                        i.iconfont.icon-fasong
                        | {{ $t('nav.sendEC') }}
                li
                    a(@click='showChat')
                        i.iconfont.icon-iconfontcommentfill
                        .circle(v-if='isMes')
                        | {{ $t('nav.info') }}
                li(v-if='isLogin')
                    a(@click='Logout')
                        i.iconfont.icon-tuichu
                        | {{$t('nav.logout')}}
                li(v-if='!isLogin')
                    a(@click='goLogin') 
                        i.iconfont.icon-lituizhi-
                        | {{$t('nav.login')}}
        //- 提示信息
        transition(name="fade"
        enter-active-class="fadeInDown"
        leave-active-class="fadeOutUp")
            info-box(:color='color' :content='content' v-if='infoShow')
        //- 发送信息窗口
        transition(name='fade')
            send-msg(v-if='$store.state.sendMsg')
        //- transition(name='fade')
        talk-box(:chatList='chatList' v-if='$store.state.chatBox')
        transition(name='fade')
            .loading(v-if='isChat')
                .iconfont.icon-jishi-copy-copy
                p 加载中
        //- 发送EC窗口
        transition(name="fade")
            .sendec-mask(v-if='boxShow')
                transition(name="fade"
                enter-active-class="fadeInDown"
                leave-active-class="fadeOutUp")
                    .sendec-mask-box
                        .sendec-mask-box-tit
                            h4 发送EC
                            .close(@click='hideBox') ×
                        .sendec-mask-box-content

                            .sendec-mask-box-content-item
                                .title 接收者
                                input(placeholder='请输入接收者的EC钱包地址'
                                 v-model='account'
                                 @blur='checkAccount'
                                 :class='accountErro ? "input-erro" : ""')

                            .sendec-mask-box-content-item(v-if='publicKeyBool')
                                .title 公钥
                                textarea(placeholder='请输入接收者的公钥'
                                :class='accountErro ? "input-erro" : ""'
                                v-model='publicKey')

                            .sendec-mask-box-content-item
                                .title 数额
                                input(placeholder='请输入发送的EC数额'
                                v-model='nums')

                            .sendec-mask-box-content-item
                                .title 费用
                                input(placeholder='请输入发送EC的费用'
                                v-model='fee')

                            .sendec-mask-box-content-item
                                .title 密码
                                input(placeholder='请输入您的账号密码' 
                                type='password'
                                v-model='pwd')
                        .sendec-mask-box-btn
                            button.send(@click='sendEC') 发送EC
                            button.cancel(@click='hideBox') 取消
        
</template>

<script>
import '$assets/scss/public/nav.scss'
import infoBox from '$com/public/infoBox'
import talkBox from '$com/public/talkBox'
import sendMsg from '$com/public/sendMsg'
import { getEC,postEC,formatDate } from '@/config/http.js'

export default {
    created() {
        if(sessionStorage.getItem('account')) {
            this.isLogin = true;
        }else {
            this.isLogin = false;
        }
        this.messageTimer();
        this.timer=setInterval(()=>{
            this.messageTimer()
        },10000)
    },
    destroyed() {
        clearInterval(this.timer);
        this.timer=null;
    },
    data() {
        return {
            timer:null,
            isMes:false,
            // 提示框
            infoShow:false,
            color:'i-red',
            content:'没有消息',

            img: require('$assets/img/logo.png'),
            boxShow:false,
            publicKeyBool:false,
            // 表单
            account:'',// 接收者账号
            accountErro:false,
            publicKey:'',// 公钥
            nums:'',// 接收数额
            fee:'',// 费用
            pwd:'',// 密码
            isChat:false,
            chatList:[],
            isLogin:false,//是否登录
        }
    },
    components:{
        infoBox,
        talkBox,
        sendMsg
    },
    methods: {
        goApi() {
            window.open('http://localhost:7876/test')
        },
        goShell() {
            window.open('http://localhost:7876/dbshell')
        },
        showBox() { // 显示发送框
            if(!sessionStorage.getItem('account')) {
                this.infoShow=true;
                this.color='i-red';
                this.content="请登录后再进行发送EC币操作！";
                setTimeout(()=> {
                    this.infoShow=false;
                },2000)
                return false;
            }
            this.boxShow = true;
            this.account='';
            this.accountErro=false;
        },
        hideBox() { // 隐藏发送框
            this.boxShow = false;
        },
        showChat() {
            if(!sessionStorage.getItem('account')) {
                this.infoShow=true;
                this.color='i-red';
                this.content="请登录后再查看通知信息！";
                setTimeout(()=> {
                    this.infoShow=false;
                },2000)
                return false;
            }
            localStorage.setItem('isMes',0);
            this.isMes = false;
            this.isChat = true;
            this.getMessage();
            // this.$store.commit('changeChat',true)
        },
        async checkAccount() {//获取账户信息
            if(this.account.length<23) {
                this.infoShow=true;
                this.color='i-red';
                this.content='请填写正确的钱包地址！'
                setTimeout(()=> {
                    this.infoShow=false;
                },2000)
                this.accountErro = true;
                return false;
            }else {
                this.accountErro = false;
            }

            try {
                let res = await getEC({ // 检查接收用户信息
                    requestType: 'getAccount',
                    account:this.account,
                    random:Math.random()
                })
                if(res.data.errorCode == 4) {
                    this.infoShow=true;
                    this.color='i-red';
                    this.content="钱包地址不正确";
                    this.accountErro = true;
                    setTimeout(()=> {
                        this.infoShow=false;
                    },2000)
                    return false;
                }else if(res.data.errorCode == 5 && this.publicKey == '') {
                    this.infoShow=true;
                    this.color='i-gray';
                    this.content="收账账户是一个未知用户，请填写公钥增加安全性";
                    setTimeout(()=> {
                        this.infoShow=false;
                    },2000)
                    this.publicKeyBool = true;
                }else if(!res.data.errorCode) {
                    this.publicKeyBool = false;
                    this.publicKey=res.data.publicKey
                }
            }
            catch (e) {
                console.log(e);
            }
        },
        async sendEC() {//判断资料填写
            this.checkAccount().then((result)=> {
                if(result) {
                    return true;
                }
                if(this.nums=='') {
                    this.infoShow=true;
                    this.color='i-red';
                    this.content="请填写发送数额";
                    setTimeout(()=> {
                        this.infoShow=false;
                    },2000)
                    return false;
                }
                if(this.fee=='') {
                    this.infoShow=true;
                    this.color='i-red';
                    this.content="请填写费用";
                    setTimeout(()=> {
                        this.infoShow=false;
                    },2000)
                    return false;
                }
                if(this.pwd=='') {
                    this.infoShow=true;
                    this.color='i-red';
                    this.content="请填写密码";
                    setTimeout(()=> {
                        this.infoShow=false;
                    },2000)
                    return false;
                }
                this.enterSend();
            })

            

        },
        async  enterSend() { // 密码登录EC
            try {
                let res = await postEC({ 
                    requestType: 'getAccountId',
                    secretPhrase:this.pwd
                })
                // sessionStorage.setItem('publicKey',res.data.publicKey); //储存publicKey
                if(res.data.errorCode==3) {
                    this.infoShow=true;
                    this.color='i-red';
                    this.content='请填写密码';
                    setTimeout(()=> {
                        this.infoShow=false;
                    },2000)
                    return false;
                }
                if(res.data.account!=sessionStorage.getItem('account')) {
                    this.infoShow=true;
                    this.color='i-red';
                    this.content='当前密码不正确';
                    setTimeout(()=> {
                        this.infoShow=false;
                    },2000)
                    return false;
                }
                this.send();
            }
            catch (e) {
                console.log(e);
            }
        },
        async send() {//发送EC
            try {
                let res = await postEC({ 
                    requestType: 'sendMoney',
                    recipient:this.account,
                    secretPhrase:this.pwd,
                    deadline:1440,
                    phased:false,
                    phasingLinkedFullHash:'',
                    phasingHashedSecret:'',
                    phasingHashedSecretAlgorithm:2,
                    publicKey:this.publicKey,
                    feeNQT:parseFloat(this.fee)*100000000,
                    amountNQT:parseFloat(this.nums)*100000000,
                })
                if(res.data.errorCode) {
                    this.infoShow=true;
                    this.color='i-red';
                    this.content=res.data.errorDescription;
                    setTimeout(()=> {
                        this.infoShow=false;
                    },2000)
                    this.boxShow = false;
                    this.account = '';
                    this.nums = '';
                    this.fee = '';
                    this.pwd = '';
                }else {
                    this.infoShow=true;
                    this.color='i-green';
                    this.content='发送EC成功！';
                    setTimeout(()=> {
                        this.infoShow=false;
                    },2000)
                    this.boxShow = false;
                    this.account = '';
                    this.nums = '';
                    this.fee = '';
                    this.pwd = '';
                }
            }
            catch (e) {
                console.log(e);
            }
        },
        // async enterSend() {//发送EC
        //     try {
        //         let res = await postEC({ 
        //             requestType: 'sendMoney',
        //             recipient:this.account,
        //             secretPhrase:this.pwd,
        //             deadline:1440,
        //             phased:false,
        //             phasingLinkedFullHash:'',
        //             phasingHashedSecret:'',
        //             phasingHashedSecretAlgorithm:2,
        //             publicKey:this.publicKey,
        //             feeNQT:parseFloat(this.fee)*100000000,
        //             amountNQT:parseFloat(this.nums)*100000000,
        //         })
        //         if(res.data.errorCode) {
        //             this.infoShow=true;
        //             this.color='i-red';
        //             this.content=res.data.errorDescription;
        //             setTimeout(()=> {
        //                 this.infoShow=false;
        //             },2000)
        //             this.boxShow = false;
        //             this.account = '';
        //             this.nums = '';
        //             this.fee = '';
        //             this.pwd = '';
        //         }else {
        //             this.infoShow=true;
        //             this.color='i-green';
        //             this.content='发送EC成功！';
        //             setTimeout(()=> {
        //                 this.infoShow=false;
        //             },2000)
        //             this.boxShow = false;
        //             this.account = '';
        //             this.nums = '';
        //             this.fee = '';
        //             this.pwd = '';
        //         }
        //     }
        //     catch (e) {
        //         console.log(e);
        //     }
        // },
        async messageTimer() {//获取账户信息
            try {
                let res = await getEC({ // 检查接收用户信息
                    requestType: 'getBlockchainTransactions',
                    account:sessionStorage.getItem('account'),
                    firstIndex:0,
                    lastIndex:75,
                    type:1,
                    subtype:0,
                    random:Math.random()
                })
                if(res) {
                    if(res.data.transactions[0].attachment.message) {
                        if(localStorage.getItem('info')) {
                            if(parseInt(localStorage.getItem('info'))<res.data.transactions[0].timestamp) {
                                localStorage.setItem('info',res.data.transactions[0].timestamp);
                                localStorage.setItem('isMes',1);
                            }
                            
                        }else {
                            localStorage.setItem('info',res.data.transactions[0].timestamp);
                        }
                    }
                    if(localStorage.getItem('isMes')=='1') {
                        this.isMes = true;
                    }
                }
                
            }
            catch (e) {
                console.log(e);
            }
        },
        async getMessage() {//获取账户信息
            try {
                let res = await getEC({ // 检查接收用户信息
                    requestType: 'getBlockchainTransactions',
                    account:sessionStorage.getItem('account'),
                    firstIndex:0,
                    lastIndex:75,
                    type:1,
                    subtype:0,
                    random:Math.random()
                })
                if(res) {
                    this.chatList=res.data.transactions;
                    setTimeout(()=>{
                        if(res.data.errorCode == 4) {
                        }
                        if(res.data.transactions.length<1) {
                            this.infoShow=true;
                            this.color='i-gray';
                            this.content="该账户没有新消息,您可以发送消息";
                            setTimeout(()=> {
                                this.infoShow=false;
                            },2000)
                            this.$store.commit('changeSendMsg',true);
                        }else {
                            this.$store.commit('changeChat',true);
                        }
                        this.isChat=false;
                    },1000)  
                }
                
            }
            catch (e) {
                console.log(e);
            }
        },
        goLogin() {
            sessionStorage.clear();
            this.$router.push('/login');
        },
        Logout() {
            sessionStorage.clear();
            this.infoShow=true;
            this.color='i-green';
            this.content='您已经成功退出登录！';
            this.isLogin=false;
            setTimeout(()=> {
                this.infoShow=false;
            },2000)
            this.$router.push('/home');
        }
    }
}
</script>


