<template lang='pug'>
    transition(name='fade')
        #ss-mask(v-if='show')
            #ss-mask-alert
                #ss-mask-alert-tit 
                    span {{title}}
                    a#ss-mask-alert-tit-close(href='javascript:;' @click='close') ×
                #ss-mask-alert-content
                    p
                        i(:class='type+" iconfont icon-xinxi"')
                        | {{content}}
                #ss-mask-alert-enter
                    button#ss-btn(@click='close')
                        span 确认
</template>

<script>
import '@/assets/scss/public/season-msgbox.scss'
import '@/assets/scss/public/season-button.scss'
// import ssButton from '$com/public/button'

export default {
    props: {
        title: {
            default: '提示'
        },
        content: {
            required:true,
            default: '暂无提示内容'
        },
        show: {
            default:false
        },
        type: {
            default: 'success',
        },
        
    },
    data() {
        return {
        }
    },
    methods: {
        close() {
            this.show=false;
        },
        
    }
}
</script>

<style lang='scss'>
    .fade-enter-active, .fade-leave-active {
        transition: opacity .2s
    }
    .fade-enter, .fade-leave-to /* .fade-leave-active in below version 2.1.8 */ {
        opacity: 0
    }
</style>



