<template lang='pug'>
    .peers  
        peers-nav
        peers-info(:data='dataList')
        peers-list(:dataList='dataList' v-if='peersLong')
        .no-info(v-if='!peersLong')
            h4 
                i.iconfont.icon-xinxi
                | 当前没有节点
</template>

<script>
import peersNav from '$com/public/nav'
import peersInfo from '$com/Peers/info'
import peersList from '$com/Peers/list'

import { getEC,postEC } from '@/config/http.js'
export default {
    created() {
        this.getPeers();
        this.timer=setInterval(()=>{
           this.getPeers(); 
        },10000)
        this.checkLogin();
    },
    destroyed() {
        clearInterval(this.timer);
    },
    components:{
        peersNav,
        peersInfo,
        peersList
    },
    data() {
        return {
            dataList: [],
            peersLong:false,
            timer:null
        }
    },
    methods: {
        checkLogin() {
            if(sessionStorage.getItem('account')) {
                return true;
            }else {
                this.$router.push('login');
                sessionStorage.setItem('loginState',false);
            }
        },
        async getPeers() { // 密码登录EC
            try {
                let res = await getEC({ 
                    requestType: 'getPeers',
                    active:true,
                    includePeerInfo:true,
                    random:Math.random()
                })
                this.dataList=res.data.peers;
                if(this.dataList.length>0) {
                    this.peersLong = true;
                }   
            }
            catch (e) {
                console.log(e);
            }
        },
    },
    computed: {
        getPeerList() {//监听现在聊天对象
            return this.$store.state.peerList;
        }
    },
    watch: {
        getPeerList(val) {
            this.getPeers();
        }
    }
}
</script>




