<template lang='pug'>
    .home
        home-nav
        home-info(:info='info')
        home-list(:dataList='this.dataList' v-if='blocksLong')
        .home-page(v-if='blocksLong')
            button.prev(@click='prevPage' :disabled='prevDisabled') 上 15 条
            span.nowPage {{(index-15<15?'00':index-15)+' - '+index}}
            button.next(@click='nextPage' :disabled='nextDisabled') 下 15 条
        .no-info(v-if='!blocksLong')
            h4 
                i.iconfont.icon-xinxi
                | 当前没有区块信息
</template>

<script>
import homeNav from '$com/public/nav'
import homeInfo from '$com/Home/info'
import homeList from '$com/Home/list'

import { getEC,postEC,formatDate } from '@/config/http.js'

import '$assets/scss/home/page.scss'

export default {
    created() {
        this.getBlockchainStatus();
        
    },
    destroyed() {
        clearInterval(this.timer);
    },
    components:{
        homeNav,
        homeInfo,
        homeList
    },
    data() {
        return {
            index:15,
            dataList:[],
            prevDisabled:true,
            nextDisabled:false,
            info:{
                createTime:0
            },
            blocksLong:false,
            timer:null,
            timeEq:0,
        }
    },
    methods: {
        async getBlockchainStatus() { // 获取区块状态
            try {
                let res = await getEC({ 
                    requestType: 'getBlockchainStatus',
                    random:Math.random()
                })
                this.timeEq=(Date.parse(new Date())/1000)-res.data.time;
                sessionStorage.setItem('timeEq',this.timeEq);
                this.getBlocks();
                this.timer = setInterval(()=>{
                    this.getBlocks();
                },10000)
            }
            catch (e) {
                console.log(e);
            }
        },
        async getBlocks() { // 密码登录EC
            try {
                let res = await getEC({ 
                    requestType: 'getBlocks',
                    firstIndex:this.index-15,
                    lastIndex:this.index,
                    random:Math.random()
                })
                if(res.data.blocks.length<=15){
                    this.nextDisabled = true;
                }else {
                    this.nextDisabled = false;
                }
                this.dataList=res.data.blocks.slice(0,15)
                this.getInfo();
                if(this.dataList.length>0) {
                    this.blocksLong=true;
                }
            }
            catch (e) {
                console.log(e);
            }
        },
        prevPage() {
            if(this.index>15) {
                this.index-=15;
                this.getBlocks();
            }
            if(this.index<=15) {
                this.prevDisabled=true;
            }
            
        },
        nextPage() {
            if(this.index>=15) {
                this.prevDisabled=false;
            }
            this.index+=15;
            this.getBlocks();
        },
        getInfo() {
            let time = 0;
            let totalTransactions = 0;
            let totalFee = 0;
            let totalAmount = 0;
            for(let i=0;i<this.dataList.length;i++) {
                totalTransactions += this.dataList[i].numberOfTransactions;
                totalFee += this.dataList[i].totalFeeNQT/100000000;
                totalAmount += this.dataList[i].totalAmountNQT/100000000;
            }
            // 区块生成时间
            this.info.createTime = Math.round((this.dataList[0].timestamp-this.dataList[this.dataList.length-1].timestamp)/15);
            // 每小时交易数目
            
            // console.log(this.dataList[this.dataList.length-1].timestamp)
            this.info.transactions = Math.round(totalTransactions /((this.dataList[0].timestamp-this.dataList[this.dataList.length-1].timestamp)/60)*60)
            // this.info.transactions = totalTransactions
            // 每个区块平均费用
            // this.info.fee = Math.round(totalFee/15)
            this.info.fee = (totalFee/this.dataList.length).toFixed(2);
            // 每个区块平均数额
            // this.info.num = Math.round(totalAmount/15)
            this.info.num = (totalAmount/this.dataList.length).toFixed(2)
        }
    }
}
</script>


