<template lang='pug'>
    .gen-info.clearfix
        .gen-info-box
            .gen-info-box-bg.blue
                .img
                .num.timestamp {{formatDate(data.timestamp)}}
                .txt 最后同步时间
        .gen-info-box
            .gen-info-box-bg.green
                .img
                .num {{data.height}}
                .txt 高度
        .gen-info-box
            .gen-info-box-bg.orange
                .img
                .num {{data.activeCount}}
                .txt 激活锻造
        //- .info-box
        //-     .info-box-bg.yellow
        //-         .img
        //-         .num 0 s
        //-         .txt 区块生成时间
</template>

<script>
import '$assets/scss/gen/info.scss'
import { formatDate } from '@/config/http.js'

export default {
    props:['data'],
    components:{
        // homeNav
    },
    methods: {
        formatDate(data) {
            return formatDate((data+parseInt(sessionStorage.getItem('timeEq')))*1000)
        },
        toMoney(num) {
            return (num || 0).toString().replace(/(\d)(?=(?:\d{3})+$)/g, '$1,');  
        }
    }
}
</script>


