import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

const state = {
    chatBox:false,
    sendMsg:false,
    chater:null,
    chaterAccount:null,
    peerList:0,
    loginstate:false,
}

const mutations = {
    changeChat(state,val) { // 聊天窗口状态
        state.chatBox = val;
    },
    changeSendMsg(state,val) { // 发送消息状态
        state.sendMsg = val;
    },
    setChater(state,val) { // 当前聊天的人EC地址
        state.chater = val;
    },
    setChaterAccount(state,val) { // 当前聊天的人的账号
        state.chaterAccount = val;
    },
    setpeerList(state) { // 存储节点列表
        state.peerList += 1;
    },
    changeLoginState(state,val) {
        state.loginstate = val;
    }
}

export default new Vuex.Store({
    state,
    mutations
})


