<template lang='pug'>
    tr
        td 
            i.iconfont.icon-dui
            a(href='javascript:;') {{ data.address }}
        td {{ data.weight }}
        td {{ data.downloadedVolume }}
        td {{ data.uploadedVolume }}
        td 
            span.app EC {{ data.version }}
        td {{ data.platform }}
        td {{ '' }}
        td 
            button(@click='addPeers').link 连接
            button(@click='addblackList') 黑名单
        transition(name="fade"
        enter-active-class="fadeInDown"
        leave-active-class="fadeOutUp")
            info-box(:color='color' :content='content' v-if='infoShow')
        
</template>

<script>
import '$assets/scss/peers/list.scss'
import { getEC,postEC } from '@/config/http.js'
import infoBox from '$com/public/infoBox'
export default {
    props:['data'],
    components:{
        infoBox
    },
    data() {
        return {
            // 提示框
            infoShow:false,
            color:'i-red',
            content:'没有消息',
        }
    },
    methods: {
        async addPeers() { // 添加节点
            try {
                let res = await postEC({ 
                    requestType: 'addPeer',
                    peer:this.data.address,
                })
                if(res.data.errorCode) {
                    this.infoShow=true;
                    this.color='i-red';
                    this.content=res.data.errorDescription;
                    setTimeout(()=> {
                        this.infoShow=false;
                    },2000)
                }else {
                    this.infoShow=true;
                    this.color='i-green';
                    this.content='添加节点成功！';
                    this.$store.commit('setpeerList');
                    setTimeout(()=> {
                        this.infoShow=false;
                    },2000)
                    this.address='';
                    this.peerStatus=false;
                } 
            }
            catch (e) {
                console.log(e);
            }
        },
        addblackList() {
            this.infoShow=true;
            this.color='i-green';
            this.content='加入黑名单成功！';
            setTimeout(()=> {
                this.infoShow=false;
                this.addblackList1();
            },2000)
        },
        async addblackList1() { // 加入黑名单
            try {
                let res = await postEC({ 
                    requestType: 'blacklistPeer',
                    peer:this.data.address,
                })
                if(res.data.errorCode) {
                    this.infoShow=true;
                    this.color='i-red';
                    this.content=res.data.errorDescription;
                    setTimeout(()=> {
                        this.infoShow=false;
                    },2000)
                    this.peerDisabled = false;
                }else {
                    this.$store.commit('setpeerList');
                    this.address='';
                    this.peerStatus=false;
                } 
            }
            catch (e) {
                console.log(e);
            }
        },
    }
    
}
</script>



