<template lang='pug'>
    .sendec-mask
        .sendec-mask-box
            .sendec-mask-box-tit
                h4 发送消息
                .close(@click='hideBox') ×
            .sendec-mask-box-content

                .sendec-mask-box-content-item
                    .title 接收者
                    input(placeholder='请输入接收者的EC钱包地址'
                        v-model='account')

                .sendec-mask-box-content-item
                    .title 发送内容
                    textarea(placeholder='请输入发送内容'
                    v-model='content')
                .sendec-mask-box-content-item
                    .title 费用
                    input(placeholder='请输入发送消息的费用'
                    v-model='fee')
                .sendec-mask-box-content-item
                    .title 密码
                    input(placeholder='请输入您的账号密码' 
                    type='password'
                    v-model='pwd')
            .sendec-mask-box-btn
                button.send(@click='sendEC') 发送消息
                button.cancel(@click='hideBox') 取消
        //- 提示信息
        transition(name="fade"
        enter-active-class="fadeInDown"
        leave-active-class="fadeOutUp")
            info-box(:color='color' :content='info' v-if='infoShow')

  
</template>

<script>
import { postEC,formatDate } from '@/config/http.js'
import infoBox from '$com/public/infoBox'

export default {
    props:['data'],
    created() {
        this.chat=this.data
    },
    components:{infoBox},
    data() {
        return {
            tags:null,
            chat:[],
            account:'',
            content:'',
            fee:'',
            pwd:'',
            // 提示信息
            infoShow:false,
            color:'i-gray',
            info:''
        }
    },
    methods: {
        hideBox() {
            this.$store.commit('changeSendMsg',false)
        },
        async send() {
            try {
                let res = await postEC({
                    requestType:'sendMessage',
                    recipient:this.account,
                    message:this.content,
                    secretPhrase:this.pwd,
                    deadline:1440,
                    phased:false,
                    phasingLinkedFullHash:'',
                    phasingHashedSecret:'',
                    phasingHashedSecretAlgorithm:2,
                    publicKey:'',
                    messageIsText:true,
                    feeNQT:parseFloat(this.fee)*100000000
                })
                if(res.data.errorCode) {
                    this.infoShow=true;
                    this.color='i-red';
                    this.info='发送错误，请检查密码或数目！';
                    setTimeout(()=> {
                        this.infoShow=false;
                    },2000)
                }else {
                    // this.chatData.push({
                    //     date:123,
                    //     msg:this.msg,
                    //     rec:this.$store.state.chaterAccount,
                    //     type:1
                    // })
                    this.infoShow=true;
                    this.color='i-green';
                    this.info='发送消息成功！';
                    this.content='';
                    this.account='';
                    this.fee='';
                    this.pwd='';
                    // this.$store.commit('changeSendMsg',false);
                    setTimeout(()=> {
                        this.infoShow=false;
                    },2000)
                }
            }
            catch (e) {
                console.log(e);
            }
        },
        async  sendEC() { // 密码登录EC
            try {
                let res = await postEC({ 
                    requestType: 'getAccountId',
                    secretPhrase:this.pwd
                })
                // sessionStorage.setItem('publicKey',res.data.publicKey); //储存publicKey
                if(res.data.errorCode==3) {
                    this.infoShow=true;
                    this.color='i-red';
                    // this.content='';
                    this.info='请填写密码'
                    setTimeout(()=> {
                        this.infoShow=false;
                    },2000)
                    return false;
                }
                if(res.data.account!=sessionStorage.getItem('account')) {
                    this.infoShow=true;
                    this.color='i-red';
                    // this.content='';
                    this.info='当前密码不正确';
                    setTimeout(()=> {
                        this.infoShow=false;
                    },2000)
                    return false;
                }
                this.send();
            }
            catch (e) {
                console.log(e);
            }
        },
    }

}
</script>



