<template lang='pug'>
    .login.clearfix
        .login-left
            .logo
        .login-right
            .logo
            .form
                .form-btn
                    button(:class='loginType==0?"active":""' @click='changeType(0)').btn1 账户
                    button(:class='loginType==1?"active":""' @click='changeType(1)').btn2 密码
                .form-box(v-if="loginType==0")
                    input(placeholder='您的EC账户,格式：EC-xxxx-xxxx-xxxx-xxxxx' 
                    v-model='account'
                    @keyup.enter='ecLogin'
                    @change='saveECAccount')
                    button(@click='ecLogin') 登录EC
                .form-box(v-if="loginType==1")
                    input(placeholder='您的密码' 
                    v-model='password' 
                    type='password'
                    @keyup.enter='pwdLogin')
                    button(@click='pwdLogin') 登录EC
            .remenber
                .select.clearfix
                    .sel-all(@click='remen')
                        .sel-box(:class='remenber?"active":""')
                        span 记住我的账户
                    router-link(to='/autoreg' tag='a').go-reg 您没有账户？点击这里创建一个
        

        transition(name='fade')
            #ss-mask(v-if='boxShow')
                #ss-mask-alert
                    #ss-mask-alert-tit 
                        span 提示
                        a#ss-mask-alert-tit-close(href='javascript:;' @click='close') ×
                    #ss-mask-alert-content
                        p
                            i(:class='type+" iconfont icon-xinxi"')
                            | {{content}}
                    #ss-mask-alert-enter
                        button#ss-btn(@click='close')
                            span 确认
        
</template>

<script>
import '$assets/scss/user/login.scss'
import { getEC,postEC } from '@/config/http.js'
// import msgBox from '$com/public/msgBox'

import '@/assets/scss/public/season-msgbox.scss'
import '@/assets/scss/public/season-button.scss'



export default {
    created() {
        if(sessionStorage.getItem('loginState')=='false') {
            this.alertBox('danger','请先登录后再进行其他操作！');
        }
        if(localStorage.getItem('ECAddress')) {
            this.remenber=true;
            this.account=localStorage.getItem('ECAddress');
        }
    },
    data() {
        return {
            remenber:false,
            // 弹窗参数
            boxShow:false,
            type:'info',
            content:'这是一条信息',

            loginType:0,
            account:'',//账号登录
            password:'',//密码登录
        }
    },
    methods: {
        saveECAccount() {
            if(this.remenber) {
                localStorage.setItem('ECAddress',this.account);
            }
        },
        close() {
            this.boxShow=false;
        },
        remen() {
            this.remenber=!this.remenber;
            if(this.remenber==true) {
                localStorage.setItem('ECAddress',this.account);
            }else {
                localStorage.removeItem('ECAddress');
            }
        },
        changeType(type) {
            this.loginType = type;
        },
        alertBox(type,content) {
            this.type=type||'info',
            this.boxShow=true;
            this.content=content;
        },
        async ecLogin() { // 账户登录EC
            try {
                let res = await postEC({ 
                    requestType: 'getAccount',
                    account: this.account,
                    random: Math.random()
                })
                if(this.account.length<10) {
                    this.alertBox('danger','请填写账号');
                    return false;
                }
                if(res.data.errorCode==3) {
                    this.alertBox('danger','请填写账号');
                    return false;
                }if(res.data.errorCode==4) {
                    this.alertBox('danger','未知账号，请输入正确的EC账户');
                    return false;
                }
                if(res.data.errorCode==5) {
                    sessionStorage.setItem('noPubKey',1); //储存公钥状态
                }
                sessionStorage.setItem('loginState',true);
                sessionStorage.setItem('account',res.data.account); //储存账号
                sessionStorage.setItem('accountRS',res.data.accountRS); //储存钱包地址
                sessionStorage.setItem('loginType','account'); //储存钱包地址
                this.$router.push('/home')
                
            }
            catch (e) {
                console.log(e);
            }
        },
        async pwdLogin() { // 密码登录EC
            try {
                let res = await postEC({ 
                    requestType: 'getAccountId',
                    secretPhrase:this.password
                })
                // sessionStorage.setItem('publicKey',res.data.publicKey); //储存publicKey
                if(res.data.errorCode==3) {
                    this.alertBox('danger','请填写账号密码');
                    return false;
                }else {
                    sessionStorage.setItem('loginState',true);
                    sessionStorage.setItem('account',res.data.account); //储存账号
                    sessionStorage.setItem('accountRS',res.data.accountRS); //储存钱包地址
                    sessionStorage.setItem('publicKey',res.data.publicKey); //储存公钥
                    sessionStorage.setItem('loginType','password');
                    this.getAccount(sessionStorage.getItem('account'));
                    this.$router.push('/home')
                }
            }
            catch (e) {
                console.log(e);
            }
        },
        async getAccount(account) { // 密码登录EC时获取账户是否绑定公钥
            try {
                let res = await postEC({ 
                    requestType: 'getAccount',
                    account:account,
                    includeAssets:true,
                    includeCurrencies:true,
                    includeLessors:true,
                    includeEffectiveBalance:true,
                    random:Math.random()
                })
                if(res.data.errorCode==5) {
                    sessionStorage.setItem('noPubKey',1); //储存公钥状态
                }else {
                    this.startDrag(account);
                }

            }
            catch (e) {
                console.log(e);
            }
        },
        async startDrag(account) { // 密码登录EC时获取账户是否绑定公钥
            try {
                let res = await postEC({ 
                    requestType: 'startForging',
                    secretPhrase:account
                })
                if(res.data.errorCode) {
                    console.log(res.data.errorCode);
                }else {
                    console.log('开始锻造...');
                    sessionStorage.setItem('isDrag',1);//锻造状态记录
                }

            }
            catch (e) {
                console.log(e);
            }
        },
    }
}
</script>



