import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter);

// 首页
import Home from '$com/Home/index'

// 节点
import Peers from '$com/Peers/index'

// 账户
import Account from '$com/Account/index'

// Gen
import Gen from '$com/Gen/index'

// 登录
import Login from '$com/user/login'

// 注册
import AutoReg from '$com/user/autoreg'

// 手动注册
import Reg from '$com/user/reg'

export default new VueRouter ({
    mode: 'hash',
    routes: [
        {
            path:'/home',
            component: Home,
        },
        {
            path:'/peers',
            component: Peers,
        },
        {
            path:'/account',
            component: Account,
        },
        {
            path:'/gen',
            component: Gen,
        },
        {
            path:'/login',
            component: Login,
        },
        {
            path:'/autoreg',
            component: AutoReg
        },
        {
            path:'/reg',
            component: Reg
        },
        {
            path:'/',
            component: Home,
        }

    ]
})


