<template lang='pug'>
    .gen
        gen-nav
        gen-info(:data='this.dataList')
        .gen-tip 
            p Information in this table is delayed by up to 30 seconds, use the desktop wallet for more up to date information
        gen-list(v-if='genLong' :dataList='this.dataList')
        .no-info(v-if='!genLong')
            h4 
                i.iconfont.icon-xinxi
                | 当前没有锻造信息
</template>

<script>
import genNav from '$com/public/nav'
import genInfo from '$com/Gen/info'
import genList from '$com/Gen/list'

import { getEC,postEC,formatDate } from '@/config/http.js'

export default {
    created() {
        this.checkLogin();
        this.getBlockchainStatus();
    },
    destroyed() {
        clearInterval(this.timer);
    },
    components:{
        genNav,
        genInfo,
        genList
    },
    data() {
        return {
            genLong:false,
            dataList:[],
            timer:null,
        }
    },
    methods: {
        async getBlockchainStatus() { // 获取区块状态
            try {
                let res = await getEC({ 
                    requestType: 'getBlockchainStatus',
                    random:Math.random()
                })
                sessionStorage.setItem('timeEq',(Date.parse(new Date())/1000)-res.data.time);
                this.getGen();
                this.timer=setInterval(()=>{
                    this.getGen();
                },10000)
            }
            catch (e) {
                console.log(e);
            }
        },
        checkLogin() {
            if(sessionStorage.getItem('account')) {
                return true;
            }else {
                this.$router.push('login');
                sessionStorage.setItem('loginState',false);
            }
        },
        async getGen() { // 密码登录EC
            try {
                let res = await getEC({ 
                    requestType: 'getNextBlockGenerators',
                    limit:10,
                    random:Math.random()
                })
                this.dataList=res.data
                if(this.dataList.generators.length>0) {
                    this.genLong=true;
                }
            }
            catch (e) {
                console.log(e);
            }
        },
    }
}
</script>


