<template lang='pug'>
    .info.clearfix
        .peers-add
            button.add(@click='showPeer') 添加节点
        .info-box
            .info-box-bg.blue
                .img
                .num {{totalUp}}
                .txt 上传量
        .info-box
            .info-box-bg.green
                .img
                .num {{totalDown}}
                .txt 下载量
        .info-box
            .info-box-bg.orange
                .img
                .num {{upToDate}}
                .txt 已连接的节点
        .info-box
            .info-box-bg.yellow
                .img
                .num {{upToDate +'/'+data.length}}
                .txt 最新节点
        //- 提示信息
        transition(name="fade"
        enter-active-class="fadeInDown"
        leave-active-class="fadeOutUp")
            info-box(:color='color' :content='content' v-if='infoShow')
        //- 添加节点窗口
        transition(name="fade")
            .sendec-mask(v-if='peerStatus')
                .sendec-mask-box
                    .sendec-mask-box-tit
                        h4 添加节点
                        .close(@click='hidePeer') ×
                    .sendec-mask-box-content
                        .sendec-mask-box-content-item
                            .title 节点地址
                            input(placeholder='请输入节点地址'
                                v-model='address'
                                @keyup.enter='addPeers')
                    .sendec-mask-box-btn
                        button.send(@click='addPeers' :disabled='peerDisabled') 添加节点
                        button.cancel(@click='hidePeer') 取消
</template>

<script>
import '$assets/scss/peers/info.scss'
import infoBox from '$com/public/infoBox'
import { getEC,postEC } from '@/config/http.js'

export default {
    mounted() {
        // let totalUp=0,totalDown=0,upToDate=0;
        console.log(this.data)
        // for(let i = 0;i<this.data.length;i++) {
        //     this.totalUp+=this.data[i].uploadedVolume;
        //     this.totalDown+=this.data[i].downloadedVolume;
        //     if(this.data[i].state==1) {
        //         this.upToDate++;
        //     } 
        // }
        // if(this.totalUp>1000000) {
        //     this.totalUp=Math.floor(this.totalUp/1000000)+'MB'
        // }else if(this.totalUp>1000) {
        //     this.totalUp=Math.floor(this.totalUp/1000)+'KB'
        // }else if(this.totalUp) {
        //     this.totalUp=Math.floor(this.totalUp)+'B'
        // }


        // if(this.totalDown>1000000) {
        //     this.totalDown=Math.floor(this.totalDown/1000000)+'MB'
        // }else if(this.totalDown>1000) {
        //     this.totalDown=Math.floor(this.totalDown/1000)+'KB'
        // }else if(this.totalDown) {
        //     this.totalDown=Math.floor(this.totalDown)+'B'
        // }
        // console.log(this.totalUp)
    },
    updated() {
        console.log(this.data)
        let temp=0,temp1=0,up=0;
        for(let i = 0;i<this.data.length;i++) {
            temp+=this.data[i].uploadedVolume;
            temp1+=this.data[i].downloadedVolume;
            if(this.data[i].state==1) {
                up++;
            } 
        }
        if(temp>1000000) {
            this.totalUp=Math.floor(temp/1000000)+'MB'
        }else if(temp>1000) {
            this.totalUp=Math.floor(temp/1000)+'KB'
        }else if(temp) {
            this.totalUp=Math.floor(temp)+'B'
        }
        console.log(temp1)

        if(temp1>1000000) {
            this.totalDown=Math.floor(temp1/1000000)+'MB'
        }else if(temp1>1000) {
            this.totalDown=Math.floor(temp1/1000)+'KB'
        }else if(temp1) {
            this.totalDown=Math.floor(temp1)+'B'
        }
        this.upToDate=up;
    },
    data() {
        return {
            totalUp:0,
            totalDown:0,
            upToDate:0,
            peerStatus:false,
            peerDisabled:false,//添加按钮是否可点
            address:'',
            // 提示框
            infoShow:false,
            color:'i-red',
            content:'没有消息',
        }
    },
    methods: {
        showPeer() {
            this.peerStatus = true;
        },
        hidePeer() {
            this.peerStatus = false;
        },
        async addPeers() { // 添加节点
            this.peerDisabled = true;
            try {
                let res = await postEC({ 
                    requestType: 'addPeer',
                    peer:this.address,
                    adminPassword:'',
                    feeNQT:0
                })
                if(res.data.errorCode) {
                    this.infoShow=true;
                    this.color='i-red';
                    this.content=res.data.errorDescription;
                    setTimeout(()=> {
                        this.infoShow=false;
                    },2000)
                    this.peerDisabled = false;
                }else {
                    this.infoShow=true;
                    this.color='i-green';
                    this.content='添加节点成功！';
                    this.$store.commit('setpeerList');
                    setTimeout(()=> {
                        this.infoShow=false;
                    },2000)
                    this.address='';
                    this.peerStatus=false;
                    this.peerDisabled = false;
                } 
            }
            catch (e) {
                console.log(e);
            }
        },
    },
    props:['data'],
    components:{
        infoBox
    }
}
</script>





