<template lang='pug'>
    tr
        td {{data.accountRS}}
            
        td {{toMoney(data.effectiveBalanceEC)}}
        td {{formatDate(data.hitTime)}}
        td {{data.deadline}}
        td {{remaining}} 
</template>

<script>
import '$assets/scss/home/list.scss'
import { formatDate } from '@/config/http.js'

export default {
    // name:{

    // },
    created() {
        this.remaining=this.data.deadline-(Date.parse(new Date())/1000-(this.timestamp+parseInt(sessionStorage.getItem('timeEq'))))+20;
        this.timer=setInterval(()=> {
            this.remaining=this.data.deadline-(Date.parse(new Date())/1000-(this.timestamp+parseInt(sessionStorage.getItem('timeEq'))))+20;
        },1000)
    },
    destroyed() {
        clearInterval(this.timer);
        this.timer=null;
    },
    props:['data','timestamp'],
    data() {
        return {
            timer:null,
            remaining:0
        }
    },
    components:{
        // homeNav
    },
    methods: {
        formatDate(data) {
            return formatDate((data+parseInt(sessionStorage.getItem('timeEq')))*1000)
        },
        toMoney(num) {
            return (num || 0).toString().replace(/(\d)(?=(?:\d{3})+$)/g, '$1,');  
        }
    }
}
</script>



