<template lang='pug'>
    .autoreg.clearfix
        transition(name='fade')
            #ss-mask(v-if='boxShow')
                #ss-mask-alert
                    #ss-mask-alert-tit 
                        span 提示
                        a#ss-mask-alert-tit-close(href='javascript:;' @click='close') ×
                    #ss-mask-alert-content
                        p
                            i(:class='type+" iconfont icon-xinxi"')
                            | {{content}}
                    #ss-mask-alert-enter
                        button#ss-btn(@click='close')
                            span 确认
        .autoreg-left
            .logo
        .autoreg-right
            .logo
            .form(v-if='!havePwd')
                .form-tit
                    h4 创建您的EC账户
                .form-txt
                    p 自动为您生成登录密码：
                .form-code
                    p {{pwd}}
                .form-tip
                    p 
                        //- i.iconfont.icon-xinxi
                        | 请您写下来或者记住这一串字符（区分大小写）。该密码用于密码登录来访问您的 EC 帐户。密码登录拥有最高权限！
                .form-tip.danger
                    p
                        i.iconfont.icon-xinxi
                        | 不要透露您的密码，如果失去它您将无法访问您的帐户！
            .form(v-if='havePwd')
                .form-tit
                    h4 确认登录密码并登陆
                .form-txt
                    p 请输入刚才的密码：
                .form-txt
                    input(v-model='password')
                .form-tip
                    p 
                        //- i.iconfont.icon-xinxi
                        | 请您写下来或者记住这一串字符（区分大小写）。该密码用于密码登录来访问您的 EC 帐户。密码登录拥有最高权限！
                .form-tip.danger
                    p
                        i.iconfont.icon-xinxi
                        | 不要透露您的密码，如果失去它您将无法访问您的帐户！
            .autoreg-right-btn.clearfix(v-if='!havePwd')
                button(@click='goNext').next 下一步
                button.cancel(@click='cancel') 取消
            .autoreg-right-btn.clearfix(v-if='havePwd')
                button.next(@click='checkLogin') 检查并登录
                button.cancel(@click='cancel') 取消
        //- .autoreg-right
        //-     .check-login
        //-         input(placeholder='请输入您的密码')
            //- .autoreg-right-goreg
            //-     router-link(to='/autoreg' tag='a').go-reg 想选择您自己的密码？请单击此处
        
</template>

<script>
import '$assets/scss/user/autoreg.scss'
import md5 from 'js-md5'
import { getEC,postEC } from '@/config/http.js'

export default {
    created() {
        let a = Date.parse(new Date);
        let b = Math.random().toString().slice(2,10);
        this.pwd = 'EC-'+md5(a+b).toUpperCase();
    },
    data() {
        return {
            remenber:false,
            loginType:0,
            account:'123',
            pwd:'',//获取的密码
            password:'',//填写的密码
            havePwd:false,
            // 弹窗参数
            boxShow:false,
            type:'info',
            content:'这是一条信息',
        }
    },
    methods: {
        cancel() {
            this.$router.push('/login');
        },
        close() {
            this.boxShow=false;
        },
        goNext() {
            this.havePwd = true;
        },
        alertBox(type,content) {
            this.type=type||'info',
            this.boxShow=true;
            this.content=content;
        },
        remen() {
            this.remenber=!this.remenber;
        },
        changeType(type) {
            this.loginType = type;
        },
        goLogin() {
            console.log(this.account)
        },
        checkLogin() {
            if(this.pwd!=this.password) {
            this.alertBox('danger','密码错误，现在返回重新查看！');
            this.havePwd=false;
            }else {
                this.pwdLogin();
            }
        },
        async pwdLogin() { // 密码登录EC
            try {
                let res = await postEC({ 
                    requestType: 'getAccountId',
                    secretPhrase:this.password
                })
                // sessionStorage.setItem('publicKey',res.data.publicKey); //储存publicKey
                if(res.data.errorCode==3) {
                    this.alertBox('danger','请填写账号密码');
                    return false;
                }else {
                    sessionStorage.setItem('loginState',true);
                    sessionStorage.setItem('account',res.data.account); //储存账号
                    sessionStorage.setItem('accountRS',res.data.accountRS); //储存钱包地址
                    sessionStorage.setItem('publicKey',res.data.publicKey); //储存公钥
                    sessionStorage.setItem('loginType','password');
                    this.getAccount(sessionStorage.getItem('account'));
                    this.$router.push('/home')
                }
            }
            catch (e) {
                console.log(e);
            }
        },
        async getAccount(account) { // 密码登录EC时获取账户是否绑定公钥
            try {
                let res = await postEC({ 
                    requestType: 'getAccount',
                    account:account,
                    includeAssets:true,
                    includeCurrencies:true,
                    includeLessors:true,
                    includeEffectiveBalance:true,
                    random:Math.random()
                })
                if(res.data.errorCode==5) {
                    sessionStorage.setItem('noPubKey',1); //储存公钥状态
                }

            }
            catch (e) {
                console.log(e);
            }
        },
    },
    
}
</script>



