<template lang='pug'>
    .account
        account-nav
        .container-box
            .account-info(v-if='noPubKey')
                p(v-if='!loginType')
                    i.iconfont.icon-xinxi
                    | 欢迎来到您的新EC账户。当第一次为帐，可以通过提供公钥来增加安全性，公钥是
                    b {{publicKey}}
                p(v-if='loginType')
                    i.iconfont.icon-xinxi
                    | 欢迎来到您的新EC账户。当第一次为帐，可以通过提供公钥来增加安全性，请使用密码登录来获取公钥
            .account-account 账户信息
            //-    span {{data.name}}
            .account-data.clearfix
                .account-data-box1
                    i {{data.unconfirmedBalanceNQT}}
                    | EC
                .account-data-box2.clearfix
                    span.sp1(v-if='peerLink' @click='goPeers')
                        i.iconfont.icon-dui
                        | 已连接
                    span.sp1(v-if='!peerLink' @click='goPeers')
                        i.iconfont.icon-cha
                        | 未连接
                    span.sp2(v-if='isfrog' @click='showFrog')
                        i.iconfont.icon-dui
                        | 已锻造
                    span.sp2(v-if='!isfrog' @click='showFrog')
                        i.iconfont.icon-cha
                        | 未锻造
                .account-data-box3
                    span 高度
                    i {{blocksData.numberOfBlocks-1}}
                .account-data-box4
                    span 钱包地址：
                    i {{data.accountRS}}
                button.account-data-btn(v-clipboard:copy="data.accountRS" 
                v-clipboard:success="onCopy"
                v-clipboard:error="onError") 复制地址
            .account-log.clearfix
                .account-log-tit 最近交易记录
                //- a.account-log-more 更多>>
            List(:dataList='dataList')
            .home-page(v-if='dataLong')
                button.prev(@click='prevPage' :disabled='prevDisabled') 上 15 条
                span.nowPage {{(index-15<15?'00':index-15)+' - '+index}}
                button.next(@click='nextPage' :disabled='nextDisabled') 下 15 条

            .no-info(v-if='!dataLong' style='padding-top:50px;')
                h4 
                    i.iconfont.icon-xinxi
                    | 当前没有交易记录
        transition(name="fade"
        enter-active-class="fadeInDown"
        leave-active-class="fadeOutUp")
            info-box(:color='color' :content='content' v-if='infoShow')
        //- 停止锻造
        transition(name="fade")
            .sendec-mask(v-if='frogBox')
                .sendec-mask-box
                    .sendec-mask-box-tit
                        h4 停止锻造
                        .close(@click='hideFrog') ×
                    .sendec-mask-box-content
                        .sendec-mask-box-content-item
                            .title 密码
                            input(placeholder='如果想要停止锻造，请输入密码'
                                v-model='pwd'
                                type='password'
                                @keyup.enter='stopFroging')
                    .sendec-mask-box-btn
                        button.send(@click='stopForging') 停止锻造
                        button.cancel(@click='hideFrog') 取消
        //- 开始锻造
        transition(name="fade")
            .sendec-mask(v-if='frogBox1')
                .sendec-mask-box
                    .sendec-mask-box-tit
                        h4 开始锻造
                        .close(@click='hideFrog1') ×
                    .sendec-mask-box-content
                        .sendec-mask-box-content-item
                            .title 密码
                            input(placeholder='如果想要开始锻造，请输入密码'
                                v-model='pwd1'
                                type='password'
                                @keyup.enter='startForging')
                    .sendec-mask-box-btn
                        button.send(@click='startForging') 开始锻造
                        button.cancel(@click='hideFrog1') 取消
            
        
</template>

<script>
import '$assets/scss/account/index.scss'

import accountNav from '$com/public/nav'
import List from '$com/Account/list'
import infoBox from '$com/public/infoBox'

import { getEC,postEC,formatDate } from '@/config/http.js'

export default {
    created() {
        this.checkLogin();
        this.getfroging();
        this.getPeers();
        if(sessionStorage.getItem('noPubKey')) {
            this.noPubKey=true;
            this.publicKey=sessionStorage.getItem('publicKey');
        }else {
            this.noPubKey=false;
        }
        if(sessionStorage.getItem('loginType')=='account') {
            this.loginType = true;// ec地址登录
        }else {
            this.loginType = false;// 密码登录
        }
        this.getBlockchainStatus();
    },
    destroyed() {
        if(this.timer) {
            clearInterval(this.timer);
            this.timer = null;
        }
    },
    components:{
        accountNav,
        List,
        infoBox
    },
    data() {
        return {
            data:{},
            blocksData:{},
            dataList:[],
            tempList:[],
            color:'i-gray',
            content:'success',
            infoShow:false,
            noPubKey:true,
            loginType:true,
            dataLong:false,
            index:15,
            prevDisabled:true,
            nextDisabled:false,
            timer:null,
            publicKey:'',
            isfrog:false,
            peerLink:false,
            pwd:'',
            frogBox:false,
            frogBox1:false,
        }
    },
    methods: {
        async getBlockchainStatus() { // 获取区块状态
            try {
                let res = await getEC({ 
                    requestType: 'getBlockchainStatus',
                    random:Math.random()
                })
                console.log(res)
                this.blocksData=res.data;
                sessionStorage.setItem('timeEq',(Date.parse(new Date())/1000)-res.data.time);
                if(sessionStorage.getItem('account')) {
                    this.getAccountInfo();
                    this.getBlockchainTransactions();
                    this.timer=setInterval(()=>{
                        this.getAccountInfo();
                        this.getBlockchainTransactions();
                        this.getfroging();
                    },10000)
                }else {
                    this.dataLong=false;
                    console.log('no account')
                }
            }
            catch (e) {
                console.log(e);
            }
        },
        async getUnconfirmedTransactions() {// 获取账户信息
            try {
                let res = await getEC({ 
                    requestType: 'getUnconfirmedTransactions',
                    account:sessionStorage.getItem('account'),
                    random:Math.random()
                })
                let arr=[];
                for(let i=0;i<res.data.unconfirmedTransactions.length;i++) {

                    arr[i]={};
                    arr[i].blockTimestamp=res.data.unconfirmedTransactions[i].timestamp;
                    arr[i].subtype=res.data.unconfirmedTransactions[i].subtype;
                    arr[i].amountNQT=res.data.unconfirmedTransactions[i].amountNQT;
                    arr[i].feeNQT=res.data.unconfirmedTransactions[i].feeNQT;
                    arr[i].height='/';
                    arr[i].confirmations='/';
                    arr[i].transaction=res.data.unconfirmedTransactions[i].transaction;

                }
                
                arr.reverse();
                console.log(this.index)
                if(this.index==15) {
                    this.dataList=arr.concat(this.tempList)
                }else {
                    this.dataList=this.tempList
                }
                
            }
            catch (e) {
                console.log(e);
            }
        },
        showFrog() {
            if(this.isfrog) {
                this.frogBox = true;
                this.frogBox1 = false;
            }else {
                this.frogBox1 = true;
                this.frogBox = false;
            }
            
        },
        hideFrog() {
            this.frogBox = false;
        },
        hideFrog1() {
            this.frogBox1 = false;
        },
        goPeers() {
            this.$router.push('/peers')
        },
        checkLogin() {
            if(sessionStorage.getItem('account')) {
                return true;
            }else {
                this.$router.push('login');
                sessionStorage.setItem('loginState',false);
            }
        },

        changeNums(num) {  
            return (num || 0).toString().replace(/(\d)(?=(?:\d{3})+$)/g, '$1,');  
        },
        prevPage() {
            if(this.index>15) {
                this.index-=15;
                this.getBlockchainTransactions();
            }
            if(this.index<=15) {
                this.prevDisabled=true;
            }
            
        },
        nextPage() {
            if(this.index>=15) {
                this.prevDisabled=false;
            }
            this.index+=15;
            this.getBlockchainTransactions();
        },
        async getAccountInfo() {// 获取账户信息
            try {
                let res = await getEC({ 
                    requestType: 'getAccount',
                    account:sessionStorage.getItem('account'),
                    includeAssets:true,
                    includeCurrencies:true,
                    includeLessors:true,
                    includeEffectiveBalance:true,
                    random:Math.random()
                })
                this.data=res.data;
                if(this.data.unconfirmedBalanceNQT) {
                    let temp = (parseFloat(this.data.unconfirmedBalanceNQT)/100000000).toFixed(3);
                    this.data.unconfirmedBalanceNQT=temp.substring(0,temp.lastIndexOf('.')+3);
                }else {
                    this.data.unconfirmedBalanceNQT=0;
                }
                
            }
            catch (e) {
                console.log(e);
            }
        },
        // async getBlockchainStatus() {//获取区块信息
        //     try {
        //         let res = await getEC({ 
        //             requestType: 'getBlockchainStatus',
        //             random:Math.random()
        //         })
        //         this.blocksData=res.data;
        //     }
        //     catch (e) {
        //         console.log(e);
        //     }
        // },
        async getBlockchainTransactions() {//获取区块信息
            try {
                let res = await getEC({ 
                    requestType: 'getBlockchainTransactions',
                    account:sessionStorage.getItem('account'),
                    firstIndex:this.index-15,
                    lastIndex:this.index,
                    random:Math.random()
                })
                this.tempList=res.data.transactions.slice(0,15);
                if(res.data.transactions.length<=15){
                    this.nextDisabled = true;
                }else {
                    this.nextDisabled = false;
                }
                if(this.tempList.length<1) {
                    this.dataLong=false;
                }else {
                    this.dataLong=true;
                }
                this.getUnconfirmedTransactions()
            }
            catch (e) {
                console.log(e);
            }
        },
        async getfroging() {//获取锻造信息
            try {
                let res = await getEC({ 
                    requestType: 'getForging',
                    random:Math.random()
                })
                console.log(res.data.generators.length);
                if(res.data.generators.length>0){
                    sessionStorage.setItem('isForging',1);
                    this.isfrog = true;
                }else {
                    this.isfrog = false;
                }
            }
            catch (e) {
                console.log(e);
            }
        },
        async getPeers() {//获取节点信息
            try {
                let res = await getEC({ 
                    requestType:'getPeers',
                    state:'CONNECTED',
                    random:Math.random()
                })
                console.log(res.data.peers.length);
                if(res.data.peers.length>0){
                    sessionStorage.setItem('peerLink',1);
                    this.peerLink = true;
                }else {
                    this.peerLink = false;
                }
            }
            catch (e) {
                console.log(e);
            }
        },
        async stop() {//获取节点信息
            try {
                let res = await postEC({ 
                    requestType:'stopForging',
                    secretPhrase:this.pwd,
                    feeNQT:0
                })
                if(res.data.errorCode){
                    this.infoShow=true;
                    this.color='i-red';
                    this.content=res.data.errorDescription
                    setTimeout(()=> {
                        this.infoShow=false;
                    },2000)
                }else if(res.data.foundAndStopped){
                    this.infoShow=true;
                    this.color='i-green';
                    this.content='现在已经停止锻造！';
                    this.frogBox=false;
                    this.pwd='';
                    setTimeout(()=> {
                        this.infoShow=false;
                    },2000)
                    this.isfrog = false;
                }else {
                    this.infoShow=true;
                    this.color='i-red';
                    this.content='停止失败，请检查！'
                    setTimeout(()=> {
                        this.infoShow=false;
                    },2000)
                }
            }
            catch (e) {
                console.log(e);
            }
        },
        async  stopForging() { // 密码登录EC
            try {
                let res = await postEC({ 
                    requestType: 'getAccountId',
                    secretPhrase:this.pwd
                })
                // sessionStorage.setItem('publicKey',res.data.publicKey); //储存publicKey
                if(res.data.errorCode==3) {
                    this.infoShow=true;
                    this.color='i-red';
                    // this.content='';
                    this.content='请填写密码'
                    setTimeout(()=> {
                        this.infoShow=false;
                    },2000)
                    return false;
                }
                if(res.data.account!=sessionStorage.getItem('account')) {
                    this.infoShow=true;
                    this.color='i-red';
                    // this.content='';
                    this.content='当前密码不正确';
                    setTimeout(()=> {
                        this.infoShow=false;
                    },2000)
                    return false;
                }
                this.stop();
            }
            catch (e) {
                console.log(e);
            }
        },
        async start() {//获取节点信息
            try {
                let res = await postEC({ 
                    requestType:'startForging',
                    secretPhrase:this.pwd1,
                    feeNQT:0
                })
                if(res.data.errorCode){
                    this.infoShow=true;
                    this.color='i-red';
                    this.content=res.data.errorDescription
                    setTimeout(()=> {
                        this.infoShow=false;
                    },2000)
                }else if(res.data.deadline>0){
                    this.infoShow=true;
                    this.color='i-green';
                    this.content='现在已经开始锻造！';
                    this.frogBox1=false;
                    this.pwd1='';
                    setTimeout(()=> {
                        this.infoShow=false;
                    },2000)
                    this.isfrog = true;
                }else {
                    this.infoShow=true;
                    this.color='i-red';
                    this.content='锻造失败，请检查密码或余额！'
                    setTimeout(()=> {
                        this.infoShow=false;
                    },2000)
                }
            }
            catch (e) {
                console.log(e);
            }
        },
        async  startForging() { // 密码登录EC
            try {
                let res = await postEC({ 
                    requestType: 'getAccountId',
                    secretPhrase:this.pwd1
                })
                // sessionStorage.setItem('publicKey',res.data.publicKey); //储存publicKey
                if(res.data.errorCode==3) {
                    this.infoShow=true;
                    this.color='i-red';
                    // this.content='';
                    this.content='请填写密码'
                    setTimeout(()=> {
                        this.infoShow=false;
                    },2000)
                    return false;
                }
                if(res.data.account!=sessionStorage.getItem('account')) {
                    this.infoShow=true;
                    this.color='i-red';
                    // this.content='';
                    this.content='当前密码不正确';
                    setTimeout(()=> {
                        this.infoShow=false;
                    },2000)
                    return false;
                }
                this.start();
            }
            catch (e) {
                console.log(e);
            }
        },
        onCopy() {
            this.infoShow=true;
            this.color='i-green';
            this.content='复制钱包地址成功，请按ctrl+v复制！'
            setTimeout(()=> {
                this.infoShow=false;
            },2000)
        },
        onError() {
            this.infoShow=true;
            this.color='i-red';
            this.content='复制钱包地址失败，请手动复制！'
            setTimeout(()=> {
                this.infoShow=false;
            },2000)
        }
    }
}
</script>




