import axios from 'axios'
import qs from 'qs'



// const ip = 'http://localhost:7876/'
const ip = '/'

const Axios = axios.create({
    baseURL: ip,
    timeout: 30000,
    responseType: 'json',
    headers: {
        'Content-Type':'application/x-www-form-urlencoded;charset=utf-8'
    }
})

Axios.interceptors.request.use(
    config => {
        if (
            config.method === "post" ||
            config.method === "put" ||
            config.method === "delete"
        ){
            config.data = qs.stringify(config.data)
        }
            return config
    },
    error => {
        console.log(erro)
        return Promise.reject(error.data.error.message)
    }
)

function get(url) {
    return body => Axios.get(url, { params: body })
}

function post(url) {
    return body => Axios.post(url, body)
}
export const formatDate = (a)=> { //格式化时间
    let now = new Date(a);
    let year=now.getFullYear();
    let month=now.getMonth()+1;
    let date=now.getDate();
    let hour=now.getHours();
    let minute=now.getMinutes();
    let second=now.getSeconds();
    if(month<10) {
        month='0'+month
    }
    if(date<10) {
        date='0'+date
    }
    if(hour<10) {
        hour='0'+hour
    }
    if(minute<10) {
        minute='0'+minute
    }
    if(second<10) {
        second='0'+second
    }
    return year+"-"+month+"-"+date+" "+hour+":"+minute+":"+second;
}

export const postEC = post('/ec'); //所有ec的Post接口
export const getEC = get('/ec'); //所有ec的get接口


