<template lang='pug'>
    transition(name='fade')
        #ss-info
            p 
                i(:class='color').iconfont.icon-xinxi
                | {{content}}

            
</template>

<script>
export default {
    created() {
        setTimeout(()=>{

        },2000);
    },
    props: {
        content: {
            required:true,
            default: '暂无提示内容'
        },
        color: {
            default:'i-green'
        }
    },
    data() {
        return {

        }
    },
    methods: {
        close() {
            this.show=false;
        },
        
    }
}
</script>

<style lang='scss'>
    #ss-info {
        z-index:2;
        position: fixed;
        top: 10%;
        left: 0;
        right: 0;
        background-color: rgba(255,255,255,1);
        margin: 0 auto;
        width: 600px;
        border-radius: 5px;
        box-shadow:0 0 10px #555;
        padding: 0 20px;
        color:#555;
        p {
            padding: 10px 0;
            i {
                padding-right: 10px;
            }
            .i-gray {
                color:#555;
            }
            .i-green {
                color:#4caf50;
            }
            .i-red {
                color:#f44336;
            }
        }


    }
    .fade-enter-active, .fade-leave-active {
        transition: opacity .2s
    }
    .fade-enter, .fade-leave-to /* .fade-leave-active in below version 2.1.8 */ {
        opacity: 0
    }
</style>



