<template lang='pug'>
    .talk-item(@click='chater' :class='isActive?"talk-item-active":""').clearfix
        .talk-item-left
            i.iconfont.icon-iconfontcommentfill
        .talk-item-right
            .talk-item-tit
                h4 {{address}}
            .talk-item-date
                p {{date}}
</template>

<script>
import '$assets/scss/public/talkbox.scss'
import item from './talkLeftItem'

export default {
    props:['address','date','account'],
    created() {
        if(this.$store.state.chater==this.address) {
            this.isActive = true;
        }
    },
    components:{
        item
    },
    data() {
        return {
            tags:null,
            isActive:false,
        }
    },
    methods: {
        chater() {
            this.$store.commit('setChater',this.address);
            this.$store.commit('setChaterAccount',this.account);
            // console.log(this.$store.state.chater)
        }
    },
    computed: {
        getNowchat() {//监听现在聊天对象
            return this.$store.state.chater
        }
    },
    watch: {
        getNowchat(val) {
            if(val==this.address) {
                this.isActive = true;
            }else {
                this.isActive = false;
            }
        }
    }

}
</script>



