<template lang='pug'>

    transition(name="fade"
    enter-active-class="fadeInDown"
    leave-active-class="fadeOutUp")
        //- 主体
        .talk-box
            .talk-box-tit 
                h4 正在和 {{$store.state.chater}} 聊天
                .close(@click='close') ×
            .talk-box-content.clearfix
                
                .talk-box-content-left
                    .talk-box-content-left-add
                        #new-chat(@click='showChat') + 新建聊天
                    .talk-box-content-left-list
                        .talk-box-content-left-list-box
                            item(v-for='(value,key,index) in chat' 
                            :key='index' 
                            :address='key'
                            :account='value[0].rec'
                            :date='formatDate(value[0].date)')
                .talk-box-content-right
                    .talk-box-content-right-top(ref='chatContent')
                        chat-item(v-for='(value,key,index) in chatData'
                        :key='index'
                        :data='value')
                    .talk-box-content-right-bottom
                        .talk-box-content-right-bottom-inp
                            textarea(placeholder='请输入想说的话...' v-model='msg')
                        .talk-box-content-right-bottom-btn.clearfix
                            .left
                                span 默认费用：1 EC
                            .right
                                //- span 密码：
                                div
                                    i.iconfont.icon-mima
                                    input(type='password' v-model='pwd' placeholder='请输入您的账户密码')
                                button(@click='enterSend') 发送
            //- 提示信息
            transition(name="fade"
            enter-active-class="fadeInDown"
            leave-active-class="fadeOutUp")
                info-box(:color='color' :content='content' v-if='infoShow')
</template>

<script>
import '$assets/scss/public/talkbox.scss'
import item from './talkLeftItem'
import infoBox from '$com/public/infoBox'
import chatItem from './chatItem'
import { postEC,formatDate } from '@/config/http.js'
export default {
    props:['chatList'],
    created() {
        // console.log(this.chatList)
        this.chatList=this.chatList.reverse();
        let nowchat,nowAccount;
        console.log(this.chatList)
        for(let i=0;i<this.chatList.length;i++) {
            if(this.chatList[i].attachment.message) {
                if(this.chatList[i].recipientRS != sessionStorage.getItem('accountRS')) {//接收者是别人
                    if(!this.chat[this.chatList[i].recipientRS]){//如果别人的地址不存在
                        this.chat[this.chatList[i].recipientRS] = []     
                    }
                    if(this.chatList[i].attachment.message) {
                        this.chat[this.chatList[i].recipientRS].push({
                            msg:this.chatList[i].attachment.message,
                            type:1,
                            date:this.chatList[i].timestamp,
                            rec:this.chatList[i].recipient
                        });
                    }
                }else { //接收者是自己
                    console.log(this.chat);
                    console.log(this.chatList[i].senderRS);
                    if(this.chat[this.chatList[i].senderRS]) { //如果发送者在数组存在
                    console.log(this.chatList[i].attachment.message);
                        if(this.chatList[i].attachment.message) {
                            this.chat[this.chatList[i].senderRS].push({
                                msg:this.chatList[i].attachment.message,
                                type:0,
                                date:this.chatList[i].timestamp,
                                rec:this.chatList[i].sender
                            });
                        }
                    }else {
                        console.log(this.chatList[i].attachment.message);
                        if(this.chatList[i].attachment.message) {
                            this.chat[this.chatList[i].senderRS] = [];
                            this.chat[this.chatList[i].senderRS].push({
                                msg:this.chatList[i].attachment.message,
                                type:0,
                                date:this.chatList[i].timestamp,
                                rec:this.chatList[i].sender
                            });
                        }
                    }
                }
            } 
        }
        // for(let i=0;i<this.chatList.length;i++) {
        //     if(this.chatList[i].recipientRS != sessionStorage.getItem('accountRS')) {//接收人不是自己

        //     }else {//接收人是自己
        //         console.log(this.chatList[i].attachment.message);
        //         if(this.chat[this.chatList[i].senderRS]) {
                    
        //         }else {
        //             if(this.chatList[i].attachment.message) {
        //                 this.chat[this.chatList[i].senderRS] = [];
        //                 this.chat[this.chatList[i].senderRS].push({
        //                     msg:this.chatList[i].attachment.message,
        //                     type:0,
        //                     date:this.chatList[i].timestamp,
        //                     rec:this.chatList[i].sender
        //                 });
        //             }
        //             console.log(this.chat);
        //         }
        //     }
        // }

        if(this.chatList[0].recipientRS!= sessionStorage.getItem('accountRS')){//默认当前聊天人为第一个
            nowchat=this.chatList[0].recipientRS;
            nowAccount=this.chatList[0].recipient;
        }else {
            nowchat=this.chatList[0].senderRS;
            nowAccount=this.chatList[0].sender;
        }
        this.$store.commit('setChater',nowchat);
        this.$store.commit('setChaterAccount',nowAccount);
        this.chat=this.chat;
        this.chatData=this.chat[nowchat];
        // console.log(this.chat)
        // console.log(this.$store.state.chaterAccount);
        // console.log(this.chatData)
        
    },
    mounted() {
        this.$refs.chatContent.scrollTop=this.$refs.chatContent.scrollHeight-400;
    },
    updated() {
        this.$refs.chatContent.scrollTop=this.$refs.chatContent.scrollHeight-400;
    },
    components:{
        item,
        chatItem,
        infoBox
    },
    data() {
        return {
            tags:null,
            chat:{},
            // nowchat:[],
            chatData:[],
            msg:'',
            pwd:'',
            infoShow:false,
            color:'i-gray',
            content:''
        }
    },
    methods: {
        close() {
            this.$store.commit('changeChat',false)
        },
        formatDate(date) {
            return formatDate((date+1515369600)*1000)
        },
        showChat() {
            this.$store.commit('changeChat',false);
            this.$store.commit('changeSendMsg',true);
        },
        async send() {//发送消息
            try {
                let res = await postEC({
                    requestType:'sendMessage',
                    recipient:this.$store.state.chaterAccount,
                    deadline:1440,
                    secretPhrase:this.pwd,
                    message:this.msg,
                    messageIsText:true,
                    feeNQT:100000000,
                })
                if(res.data.errorCode) {
                    this.infoShow=true;
                    this.color='i-red';
                    this.content=res.data.errorDescription;
                    setTimeout(()=> {
                        this.infoShow=false;
                    },2000)
                }else {
                    this.chatData.push({
                        date:123,
                        msg:this.msg,
                        rec:this.$store.state.chaterAccount,
                        type:1
                    })
                    this.infoShow=true;
                    this.color='i-green';
                    this.content='发送消息成功！';
                    setTimeout(()=> {
                        this.infoShow=false;
                    },2000)
                    this.pwd='';
                    this.msg='';
                }
            }
            catch (e) {
                console.log(e);
            }
        },
        async  enterSend() { // 密码登录EC
            try {
                let res = await postEC({ 
                    requestType: 'getAccountId',
                    secretPhrase:this.pwd
                })
                // sessionStorage.setItem('publicKey',res.data.publicKey); //储存publicKey
                if(res.data.errorCode==3) {
                    this.infoShow=true;
                    this.color='i-red';
                    this.content='请填写密码';
                    setTimeout(()=> {
                        this.infoShow=false;
                    },2000)
                    return false;
                }
                if(res.data.account!=sessionStorage.getItem('account')) {
                    this.infoShow=true;
                    this.color='i-red';
                    this.content='当前密码不正确';
                    setTimeout(()=> {
                        this.infoShow=false;
                    },2000)
                    return false;
                }
                this.send();
            }
            catch (e) {
                console.log(e);
            }
        },
    },
    computed: {
        getNowchat() {//监听现在聊天对象
            return this.$store.state.chater;
        }
    },
    watch: {
        getNowchat(val) {
            this.chatData=[];
            this.chatData=this.chatData.concat(this.chat[val]);
            console.log(this.chatData);
        }
    }

}
</script>



