<template lang='pug'>
    .account-list
        table.account-title.clearfix
            thead
                tr
                    th 交易时间
                    //- th 信息
                    th 交易类型
                    th 成交金额
                    th 交易费用
                    th 账户
                    th 高度
                    th 确认
                    
            tbody
                item(v-for='(item,key) in dataList' :key='key' :data='item')
        
</template>

<script>
import '$assets/scss/account/list.scss'
import item from '$com/Account/item'

export default {
    props:['dataList'],
    components:{
        item
        // homeNav
    }
}
</script>



