<template lang='pug'>
    
    tr
        td 
            a(href='javascript:;' @click='showBox(true)') {{data.height}}
        td {{formatDate(data.timestamp)}}
        td {{toNum(data.totalAmountNQT)}}
        td {{toNum(data.totalFeeNQT)}}
        td {{data.numberOfTransactions}}
        td 
            a(href='javascript:;' @click='showBox1(true)') {{data.generatorRS}}
        td {{data.payloadLength}} B
        .mask(v-if='show')
            .mask-box
                .mask-box-tit
                    h4 区块 {{data.height}} 详细信息
                    .close(@click='showBox(false)') ×
                .mask-box-content
                    .mask-box-content-list
                        .item
                            label 上一区块哈希：
                            p {{block.previousBlockHash}}
                        .item
                            label 载荷长度：
                            p {{block.payloadLength}}
                        .item
                            label 总数 NQT：
                            p {{block.totalAmountNQT}}
                        .item
                            label 生成签名：
                            p {{block.generationSignature}}
                        .item
                            label 生成公钥：
                            p {{block.generatorPublicKey}}
                        .item
                            label 基础目标值：
                            p {{block.baseTarget}}
                        .item
                            label 载荷哈希：
                            p {{block.payloadHash}}
                        .item
                            label 交易数目：
                            p {{block.numberOfTransactions}}
                        .item
                            label 区块签名：
                            p {{block.blockSignature}}
                        .item
                            label 版本：
                            p {{block.version}}
                        .item
                            label 总费用 NQT：
                            p {{block.totalFeeNQT}}
                        .item
                            label translation:ec_block：
                            p {{block.ecBlock}}
                        .item
                            label 累积难度：
                            p {{block.cumulativeDifficulty}}
                        .item
                            label 高度：
                            p {{block.height}}
                        .item
                            label 时间戳：
                            p {{block.timestamp}}
                        .item
                            label 产生者：
                            p {{block.generatorRS}}
                        .item
                            label 上一个区块：
                            p {{block.previousBlock}}
                        .item
                            label 下一个区块：
                            p {{block.nextBlock}}
                        .item
                            label 区块生成时间：
                            p {{formatDate(block.timestamp)}}
        
        .mask(v-if='detail')
            .mask-box1
                .mask-box1-tit
                    h4 区块 {{data.generatorRS}} 交易信息
                    .close(@click='showBox1(false)') ×
                .mask-box1-content
                    .mask-box1-content-list.clearfix
                        ul.mask-box1-content-list-tit.clearfix
                            li(style='width:20%') 日期
                            li(style='width:10%') 类型
                            li(style='width:10%') 数额
                            li(style='width:10%') 费用
                            li(style='width:25%') 发出账户
                            li(style='width:25%') 接收账户
                        ul.mask-box1-content-list-item.clearfix(v-for='(item,index) in transactions' :key='index')
                            li(style='width:20%') {{formatDate(item.timestamp)}}
                            li(style='width:10%') {{toType(item.type)}}
                            li(style='width:10%') {{toNum(item.amountNQT)}}
                            li(style='width:10%') {{toNum(item.feeNQT)}}
                            li(style='width:25%') {{item.senderRS||'/'}}
                            li(style='width:25%') {{item.recipientRS||'/'}}
</template>

<script>
import '$assets/scss/home/list.scss'

import { getEC,formatDate } from '@/config/http.js'

export default {
    props:['data'],
    components:{
        // homeNav
    },
    data() {
        return {
            show:false,
            detail:false,
            block:{},
            transactions:[]
        }
    },
    methods: {
        toNum(num) {
            let temp = (parseFloat(num)/100000000);
            if(temp==0) {
                return 0;
            }
            if(String(temp).indexOf(".")>-1) {
                if(temp.toString().split(".")[1].length<8) {
                    return parseInt(temp)+'.'+temp.toString().split(".")[1];
                }else {
                    temp = temp.toFixed(8);
                    return temp.substring(0,temp.lastIndexOf('.')+8);
                }
            }else {
                return temp;
            }
        },
        formatDate(data) {
            return formatDate((data+parseInt(sessionStorage.getItem('timeEq')))*1000)
        },
        showBox(state) {
            this.show = state;
            if(state) {
                this.getBlock();
            }
        },
        showBox1(state) {
            this.detail = state;
            if(state) {
                this.getBlockchainTransactions();
            }
        },
        toType(type) {
            if(type==0) {
                return '支付信息'
            }else if(type==1){
                return '消息信息'
            }else if(type==3){
                return '市场信息'
            }else {
                return '其他信息'
            }
        },
        async getBlock() { // 密码登录EC
            try {
                let res = await getEC({ 
                    requestType: 'getBlock',
                    includeTransactions:true,
                    height:this.data.height,
                    random:Math.random()
                })
                this.block=res.data;
            }
            catch (e) {
                console.log(e);
            }
        },
        async getBlockchainTransactions() { // 密码登录EC
            try {
                let res = await getEC({ 
                    requestType: 'getBlockchainTransactions',
                    account:this.data.generatorRS,
                    firstIndex:0,
                    lastIndex:100,
                    random:Math.random()
                })
                this.transactions=res.data.transactions;
            }
            catch (e) {
                console.log(e);
            }
        },
    }
}
</script>


