<template lang='pug'>
    tr
        td 
            a(href='javascript:;' @click='showBox(true)') {{ formatDate(data.blockTimestamp) }}
        td {{ toType(data.type) }}
        td {{ toNum1(data.amountNQT)}}
        td.fee {{ toNum(data.feeNQT) }}
        td(style='font-size:12px;') {{ data.senderRS?(data.senderRS+' -> '+data.recipientRS):'/' }}
        td {{ data.height }}
        td {{ data.confirmations>1440?'1400+':data.confirmations }}
        .mask(v-if='show')
            .mask-box
                .mask-box-tit
                    h4 {{data.transaction}} 交易信息
                    .close(@click='showBox(false)') ×
                .mask-box-content
                    .mask-box-content-list
                        .item
                            label 签名：
                            p {{list.signature}}
                        .item
                            label Transaction Index：
                            p {{list.transactionIndex}}
                        .item
                            label 类型：
                            p {{list.type}}
                        .item
                            label 分期：
                            p {{list.phased}}
                        .item
                            label EC Block Id：
                            p {{list.ecBlockId}}
                        .item
                            label 哈希签名：
                            p {{list.signatureHash}}
                        .item
                            label 发送者 RS：
                            p {{list.signature}}
                        .item
                            label 区块：
                            p {{list.block}}
                        .item
                            label 区块时间戳：
                            p {{list.blockTimestamp}}
                        .item
                            label 截止时间：
                            p {{list.deadline}}
                        .item
                            label 时间戳：
                            p {{list.timestamp}}
                        .item
                            label 发送者公钥：
                            p {{list.senderPublicKey}}
                        .item
                            label 费用 NQT：
                            p {{toNum(list.feeNQT)}} EC
                        .item
                            label 确认：
                            p {{list.confirmations}}
                        .item
                            label 完整哈希：
                            p {{list.fullHash}}
                        .item
                            label 版本：
                            p {{list.version}}
                        .item
                            label 发送者：
                            p {{list.senderRS}}
                        .item
                            label 接收者：
                            p {{list.recipientRS}}
                        .item
                            label EC Block Height：
                            p {{list.ecBlockHeight}}
                        .item
                            label 交易时间：
                            p {{formatDate(list.timestamp)}}
                        .item
                            label 区块生成时间：
                            p {{list.blockTimestamp?formatDate(list.blockTimestamp):'/'}}
                        .item
                            label 高度：
                            p {{list.height}}

        
</template>

<script>
import '$assets/scss/account/list.scss'
import { formatDate,getEC } from '@/config/http.js'

export default {
    props:['data'],
    components:{
        // homeNav
    },
    data() {
        return {
            show:false,
            list:{}
        }
    },
    methods: {
        toNum(num) {
            let temp = (parseFloat(num)/100000000);
            if(temp==0) {
                return 0;
            }
            if(String(temp).indexOf(".")>-1) {
                if(temp.toString().split(".")[1].length<8) {
                    return parseInt(temp)+'.'+temp.toString().split(".")[1];
                }else {
                    temp = temp.toFixed(8);
                    return temp.substring(0,temp.lastIndexOf('.')+8);
                }
            }else {
                return temp;
            }
        },
        toNum1(num) {
            if(this.data.senderRS==sessionStorage.getItem('accountRS')) {
                if(num==0) {
                    return '0';  
                }else {
                    let temp = (parseFloat(num)/100000000).toFixed(8);
                    return '-'+ temp.substring(0,temp.lastIndexOf('.')+8);
                }
            }else {
                if(num==0) {
                    return '0';  
                }else {
                    let temp = (parseFloat(num)/100000000).toFixed(3);
                    return '+'+ temp.substring(0,temp.lastIndexOf('.')+3);
                }
            }
            
        },
        showBox(state) {
            this.show = state;
            if(state) {
                this.getList();
            }
        },
        formatDate(data) {
            return formatDate((data+parseInt(sessionStorage.getItem('timeEq')))*1000)
        },
        toMoney(num) {
            return (num || 0).toString().replace(/(\d)(?=(?:\d{3})+$)/g, '$1,');  
        },
        async getList() { // 密码登录EC
            try {
                let res = await getEC({ 
                    requestType: 'getTransaction',
                    transaction:this.data.transaction,
                    random:Math.random()
                })
                this.list=res.data;
                console.log(res.data)
            }
            catch (e) {
                console.log(e);
            }
        },
        toType(type) {
            if(type==0) {
                return '支付信息'
            }else if(type==1){
                return '消息信息'
            }else if(type==3){
                return '市场信息'
            }else {
                return '其他信息'
            }
        },
    }
}
</script>



